/* Utilitário simples de animações, inspirado em tailwindcss-animate */

@keyframes fade-in { from { opacity: 0 } to { opacity: 1 } }
@keyframes fade-out { from { opacity: 1 } to { opacity: 0 } }
@keyframes zoom-in { from { transform: scale(0.95) } to { transform: scale(1) } }
@keyframes zoom-out { from { transform: scale(1) } to { transform: scale(0.95) } }
@keyframes slide-in-from-top { from { transform: translateY(-10px); opacity: 0 } to { transform: translateY(0); opacity: 1 } }
@keyframes slide-in-from-bottom { from { transform: translateY(10px); opacity: 0 } to { transform: translateY(0); opacity: 1 } }
@keyframes slide-in-from-left { from { transform: translateX(-10px); opacity: 0 } to { transform: translateX(0); opacity: 1 } }
@keyframes slide-in-from-right { from { transform: translateX(10px); opacity: 0 } to { transform: translateX(0); opacity: 1 } }

.animate-in { animation-duration: 200ms; animation-fill-mode: both; }
.animate-out { animation-duration: 200ms; animation-fill-mode: both; }
.fade-in { animation-name: fade-in; }
.fade-out { animation-name: fade-out; }
.zoom-in { animation-name: zoom-in; }
.zoom-out { animation-name: zoom-out; }
.slide-in-from-top { animation-name: slide-in-from-top; }
.slide-in-from-bottom { animation-name: slide-in-from-bottom; }
.slide-in-from-left { animation-name: slide-in-from-left; }
.slide-in-from-right { animation-name: slide-in-from-right; }